#bagcampus_pricing_table_home .grey:hover,
#bagcampus_pricing_table_home .midnight:hover,
#bagcampus_pricing_table_home .orange:hover,
#bagcampus_pricing_table_home .purple:hover,
#bagcampus_pricing_table_home .red:hover,
#bagcampus_pricing_table_home .turquoise:hover{
  border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
  transition: all 0.3s ease-in-out 0s;
}
#bagcampus_pricing_table_home .divider{
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
  padding: 20px;
}
#bagcampus_pricing_table_home .divider span{
  width: 100%;
  display: table;
  height: 2px;
  background: #ddd;
  margin: 50px auto;
  line-height: 2px;
}
#bagcampus_pricing_table_home .itemname{
  text-align: center;
  font-size: 50px ;
  padding: 50px 0 20px ;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
  text-decoration: none;
    font-weight: 300;
}
#bagcampus_pricing_table_home .itemnametext{
    text-align: center;
    font-size: 20px;
    padding-top: 5px;
    text-transform: uppercase;
    display: inline-block;
}
#bagcampus_pricing_table_home .footer{
  padding:40px 0;
}

#bagcampus_pricing_table .price-heading{
    text-align: center;
    margin-bottom: 2rem;
}
#bagcampus_pricing_table .price-heading h1{
  color: #666;
  margin: 0;
  /* padding: 0 0 50px 0; */
}
#bagcampus_pricing_table .demo-button {
    background-color: #333333;
    color: #ffffff;
    display: table;
    font-size: 20px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 50px;
    outline-color: -moz-use-text-color;
    outline-style: none;
    outline-width: medium ;
    padding: 10px;
    text-align: center;
    text-transform: uppercase;
}
#bagcampus_pricing_table .bottom_btn{
  background-color: #333333;
    color: #ffffff;
    display: table;
    font-size: 28px;
    margin: 60px auto 20px;
    padding: 10px 25px;
    text-align: center;
    text-transform: uppercase;
}
#bagcampus_pricing_table .demo-button:hover{
  background-color: #666;
  color: #FFF;
  text-decoration:none;
  
}
#bagcampus_pricing_table .bottom_btn:hover{
  background-color: #666;
  color: #FFF;
  text-decoration:none;
}
